<template>
	<div class="zstp_fg_page2">
		<div class="cont box-border-shadow">
			<div>
				<h1 style="color: #b80000;margin-bottom: 20px; font-size: 34px; text-align: center; font-weight: bold; line-height: 44px;">{{fgJuZi.fgBiaoti|getText}}</h1>
				<div class="text" style="color: #b80000;font-size: 16px;text-indent: 44px;line-height: 30px;">
					段落/语句：{{fgJuZi.text|getText}}
				</div>
				<div style="margin:10px 0">
				    <el-radio v-model="radio" label="1" border>实体1-关系-实体2</el-radio>
				    <el-radio v-model="radio" label="2" border>实体-属性-值</el-radio>
				    <el-radio v-model="radio" label="3" border>条件-关联词-结论</el-radio>
				    <el-radio v-model="radio" label="4" border>添加疑点</el-radio>
				</div>
				<div v-show="radio=='1'">
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
							<tr class="footer_tr">
								<td>
									实体1：
									<el-input placeholder="请输入内容" v-model="form1.word1" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
									    <el-select v-model="form1.select" slot="prepend" placeholder="请选择" style="width: 100px;">
									      <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
									    </el-select>
									</el-input>
									<!-- <el-input clearable size="small" v-model="form1.word1"></el-input> -->
								</td>
								<td>关系：
									<el-input clearable size="small" v-model="form1.relation"  style="width: 80%;margin: 0 auto"></el-input></td>
								<td>实体2：
									<el-input placeholder="请输入内容" v-model="form1.word2" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
									    <el-select v-model="form1.select2" slot="prepend" placeholder="请选择" style="width: 100px;">
									      <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
									    </el-select>
									</el-input>
								</td>
								<td><el-button size="small" @click="addPingLun(form1,1)">添 加</el-button></td>
							</tr>
					</table>
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
						<tr>
							<th>实体1</th>
							<th>关系</th>
							<th>实体2</th>
							<th>操作</th>
						</tr>
					</table>
					<div style="max-height: 600px;overflow-y: auto;">
						<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
							<tr v-for="items in lishipingjia.guanxi">
								<td>{{items[0]}} <span style="color: #b80000">({{selectList[(items[1]-1)]}})</span></td>
								<td>{{items[2]}}</td>
								<td>{{items[3]}} <span style="color: #b80000">({{selectList[(items[4]-1)]}})</span></td>
								<td><el-button size="small" @click="delPingLun(items,1)">删 除</el-button></td>
							</tr>
						</table>
					</div>
				</div>
				<div v-show="radio=='2'">
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
						<tr class="footer_tr">
							<td>实体：
								<el-input placeholder="请输入内容" v-model="form2.word1" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
								    <el-select v-model="form2.select" slot="prepend" placeholder="请选择" style="width: 100px;">
							    	  <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
								     <!--  <el-option label="业务概念实体" value="1"></el-option>
								      <el-option label="财务审计概念实体" value="2"></el-option>
								      <el-option label="组织实体" value="3"></el-option>
								      <el-option label="审计对象实体" value="4"></el-option>
								      <el-option label="其他" value="5"></el-option> -->
								    </el-select>
								</el-input>
							</td>
							<td>属性：<el-input clearable size="small" v-model="form2.relation" style="width: 80%;margin: 0 auto"></el-input></td>
							<td>值：
								<el-input placeholder="请输入内容" v-model="form2.word2" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
								    <el-select v-model="form2.select2" slot="prepend" placeholder="请选择" style="width: 100px;">
								      <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
								     <!--  <el-option label="财务审计概念实体" value="2"></el-option>
								      <el-option label="组织实体" value="3"></el-option>
								      <el-option label="审计对象实体" value="4"></el-option>
								      <el-option label="其他" value="5"></el-option> -->
								    </el-select>
								</el-input>
							</td>
							<td><el-button size="small" @click="addPingLun(form2,2)">添 加</el-button></td>
						</tr>
					</table>
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
						<tr>
							<th>实体</th>
							<th>属性</th>
							<th>值</th>
							<th>操作</th>
						</tr>
					</table>
					<div style="max-height: 600px;overflow-y: auto;">
						<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
							<tr v-for="items in lishipingjia.shuxing">
								<td>{{items[0]}} <span style="color: #b80000">({{selectList[(items[1]-1)]}})</span></td>
								<td>{{items[2]}}</td>
								<td>{{items[3]}} <span style="color: #b80000">({{selectList[(items[4]-1)]}})</span></td>
								<td><el-button size="small" @click="delPingLun(items,2)">删 除</el-button></td>
							</tr>
						</table>
					</div>
				</div>
				<div v-show="radio=='3'">
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
						<tr class="footer_tr">
							<td>条件：
								<el-input placeholder="请输入内容" v-model="form3.word1" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
								    <el-select v-model="form3.select" slot="prepend" placeholder="请选择" style="width: 100px;">
								      <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
								    </el-select>
								</el-input>
							</td>
							<td>关联词：<el-input clearable size="small" v-model="form3.relation" style="width: 80%;margin: 0 auto"></el-input></td>
							<td>结论：
								<el-input placeholder="请输入内容" v-model="form3.word2" class="input-with-select"  style="width: 80%;margin: 0 auto" clearable>
								    <el-select v-model="form3.select2" slot="prepend" placeholder="请选择" style="width: 100px;">
								      <el-option v-for="(item,index) in selectList" :label="item" :value="index+1"></el-option>
								    </el-select>
								</el-input>
							</td>
							<td><el-button size="small" @click="addPingLun(form3,3)">添 加</el-button></td>
						</tr>
					</table>
					<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
						<tr>
							<th>条件</th>
							<th>关联词</th>
							<th>结论</th>
							<th>操作</th>
						</tr>
					</table>
					<div style="max-height: 600px;overflow-y: auto;">
						<table cellpadding="0" cellspacing="0" width="100%" class="table_list">
							<tr v-for="items in lishipingjia.guanlian">
								<td>{{items[0]}} <span style="color: #b80000">({{selectList[(items[1]-1)]}})</span></td>
								<td>{{items[2]}}</td>
								<td>{{items[3]}} <span style="color: #b80000">({{selectList[(items[4]-1)]}})</span></td>
								<td><el-button size="small" @click="delPingLun(items,3)">删 除</el-button></td>
							</tr>
						</table>
					</div>
				</div>
				<div class="grid-content modal-content" v-show="radio=='4'">
                	<div class="btn-add fr" @click="yd1=!yd1">+ 添加疑点</div>
                    <div class="clear"></div>
                    <div class="text-area" :class="{hidden:yd1}">
                        <textarea v-model="ydText1" name="txt" class="area" placeholder="添加疑点" rows="5"></textarea>
                        <p style="height: 50px;">
                            <button class="fr add" type="submit" @click="addQues1">添加</button>
                        </p>
                    </div>
                    <ul class="list" v-if="getQuestionData.length>0" style="max-height: 600px;overflow-y: auto;">
                        <li v-for="(item,index) in getQuestionData">
                            <div class="hidden" id="yd-id">{{item[0]}}</div>
                            <h3>疑点{{index+1}}：<span class="yd">{{item[1]}}</span></h3>
                            <div class="fr sub" @click="appraise1(index)">提交</div>
                            <div class="am-form-group sel" style="margin-top:10px;">
                            	<el-radio-group v-model="radio1[index]">
                            	 	<el-radio :label="'0-'+item[0]">合理</el-radio>
										<el-radio :label="'1-'+item[0]">不合理</el-radio>
                            	</el-radio-group>
                            </div>
                        </li>
                    </ul>
                </div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	// import './../image/jquery.easyui.min.js'
	export default{
		name:'zstp_fg_page2',
		data(){
			return {
				faguiList:[],
			    faguiXq:[],
			    faguiJuZi:[], 
			    steps:1,
			    fgBiaoti:'',
			    fgDuanluo:'',
			    fgJuZi:'',
			    dialogFormVisible: false,
			    selectList:['业务概念实体','财务审计概念实体','组织实体','审计对象实体','其他'],
		        form1: {
		          word1:'',
		          word2:'',
		          relation:'',
		          select:'',
		          select2:''
		        },
		        form2: {
		          word1:'',
		          word2:'',
		          relation:'',
		          select:'',
		          select2:''
		        },
		        form3: {
		          word1:'',
		          word2:'',
		          relation:'',
		          select:'',
		          select2:''
		        },
		        formLabelWidth: '120px',
		        radio: '1',
		        lishipingjia:{},
		        yd1:true,
		        radio1:[],
			    ydText1:'',
			    getQuestionData:[]
			}
		},
		components:{
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
		},
		filters:{
	    	getText(value){
	    		if(value){
		    		return value.trim();
	    		}
	    	}
	    },
		methods:{
			getQuestion1(){
				let biaoshi;
				if(this.fgJuZi.index==1){
					biaoshi="shitichouqu_duanluo"
				}else if(this.fgJuZi.index==2){
					biaoshi="shitichouqu_juzi"
				}
				reqData
			        .req({
			          apiName: "showQues",
			          data: {   
			            qiye_id: biaoshi,          
			            yidian_name: this.fgJuZi.id,
			            feilei_id:biaoshi
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.getQuestionData=data;    
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
			appraise1(index){
				if(this.radio1[index]){
					let n=this.radio1[index].split("-")[0];
					let id=this.radio1[index].split("-")[1];
					console.log(n+"====="+id);
					 reqData
				        .req({
				          apiName: "appraiseQues",
				          data: {             
				            // qiye_id: this.getWord,
				            // feilei_id:'yd-'+this.id,
				            yidian_id:id,
				            num:n,
				            username:this.userName
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            alert("评价成功");
				            this.radio1=[];
				          }
				        })
				        .catch(err => {
				          console.log(err);
				       });
				}else{
					alert('请选择合理或不合理')
				}
			},
			addQues1(){
				let biaoshi;
				if(this.fgJuZi.index==1){
					biaoshi="shitichouqu_duanluo"
				}else if(this.fgJuZi.index==2){
					biaoshi="shitichouqu_juzi"
				}
				reqData
				    .req({
				      apiName: "addQues",
				      data: {             
				      qiye_id: biaoshi,
				      yidian_name: this.fgJuZi.id,
				      feilei_id:biaoshi,
				      username:this.userName,
				      yidian:this.ydText1
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       alert("添加成功");
				       this.ydText1='';
				       this.yd1=true;
				       this.getQuestion1();
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
			getDialog(item){
				if(this.userName){
					this.fgJuZi=item;
					// this.dialogFormVisible=true;
					// console.log(item);
					this.showPingLun(item.id);
					this.getQuestion1();
				}else{
					alert("请先登录");
				}
			},
			showPingLun(id){
				reqData
					.req({
				      apiName: "shitiguanxichouqu_shitiguanxi_chaxun",
				      data:{
				      	juzi_md5_id:id,
				      	juzi_duanluo:this.fgJuZi.index
				      }
				    })
				    .then(res => {
					    res = res.data;
					    if (res && res.code === "200") {
					    let data = res.data;
					    console.log(data);
					    this.lishipingjia=data;
					    }
				    })
				    .catch(err => {
				       console.log(err);
				    });
			},
			addPingLun(form,index){
				let word1=form.word1;
		    	let word2=form.word2;
		    	let relation=form.relation;
		    	let select=form.select;
		    	let select2=form.select2;
		    	let id=this.fgJuZi.id;
		    	if(!word1||!word2||!relation){
		    		alert("内容不能为空");
		    	}else if(!select||!select2){
					alert("请选择实体类别");
		    	}else{
		    		reqData
					    .req({
					      apiName: "shitiguanxichouqu_shitiguanxi",
					      data:{
					      	juzi_md5_id:id,
					      	shiti1:word1,
							shiti2:word2,
							guanxi:relation,
							shiti1_fenlei:select,
							shiti2_fenlei:select2,
							biaoshi:index,
							juzi_duanluo:this.fgJuZi.index
					      }
					    })
					    .then(res => {
					      res = res.data;
					      if (res && res.code === "200") {
					       let data = res.message;
					       alert(data);
					       this.showPingLun(id);
					    }
					    })
					    .catch(err => {
					       console.log(err);
					    });
		    	}
			},
			delPingLun(item,index){
				reqData
				    .req({
				      apiName: "shitiguanxichouqu_shitiguanxi_shanchu",
				      data:{
				      	juzi_duanluo:this.fgJuZi.index,
						juzi_md5_id:this.fgJuZi.id,
						shiti1:item[0],
						guanxi:item[2],
						shiti2:item[3],
						biaoshi:index,
				      }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       alert("删除成功");
				       this.showPingLun(this.fgJuZi.id);
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });
			}
		},
	    mounted(){
	    	this.getDialog(this.$route.query);
	    }
	}
</script>
<style scoped="">
	.zstp_fg_page2 .cont{
		width: 90%;
	    margin: 30px auto;
	    min-height: 780px;
	    padding: 40px;
	    box-sizing: border-box;
	} 
	.zstp_fg_page2 .cont .table_list td,
	.zstp_fg_page2 .cont .table_list th{
		border: 1px solid #ccc;
		text-align: center;
		line-height: 40px;
		width: 30%
	}
	.zstp_fg_page2 .cont .table_list th{
		border: none;
		background-color: rgb(184,0,0,0.7);
		color: #fff;
		line-height: 44px;
	}
	.zstp_fg_page2 .cont .table_list td:nth-child(4){
		width: 10%
	}
	.zstp_fg_page2 .cont .table_list th:nth-child(4){
		width: 10%
	}
	.zstp_fg_page2 .cont .table_list .footer_tr td{
		border: none;
		line-height: 50px;
	}

	.zstp_fg_page2 .grid-content{
		padding: 20px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 1px solid #ccc;
	    border-radius: 6px;
	    margin-bottom: 20px;
	}


	.zstp_fg_page2 .modal-content{
        /*padding-left: 16px;*/
        box-sizing: border-box;
    }
    .zstp_fg_page2 .am-form-group {
        margin-bottom: 0;
    }
   .zstp_fg_page2 .modal-content .list li{
        padding-top: 13px;
        padding-bottom: 13px;
        border-bottom: 1px solid #ebebeb;
    }
    .zstp_fg_page2 .modal-content .list li .sub{
        color: #b80000;
        cursor: pointer;
        margin-right: 10px;
    }
    .zstp_fg_page2 .modal-content .btn-add{
        color: #b80000;
        cursor: pointer;
        height: 58px;
        line-height: 58px;
    }
    .zstp_fg_page2 .modal-content  .text-area{
        width: 100%;
        margin: 0 auto;
    }
    .zstp_fg_page2 .modal-content  .text-area .area{
        width: 100%;
        border: 1px solid #ebebeb;
        color: #333;
        padding: 10px;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .zstp_fg_page2 .modal-content  .text-area button{
        outline: none;
        background-color: #b80000;
        border:none;
        padding: 6px 14px;
        margin-top: 10px;
        color: #fff;
        border-radius: 6px;
        cursor: pointer;
    }
</style>



